.rule {
  font-size: 16.5217391304px;
  font-weight: 300;
  line-height: 1.7;
}
.rule-item + .rule-item {
  margin-top: 1.3em;
  padding-top: 1.3em;
  border-top: 1.7391304348px dashed #94b33a;
}
.rule-tit {
  margin-bottom: 0.2em;
  font-size: 20.4347826087px;
  font-weight: 500;
  letter-spacing: 0.01em;
  color: #94b33a;
}
.rule-val {
  word-break: break-all;
  text-align: justify;
}
.rule-val ul,
.rule-val ol {
  margin: 0;
  padding-left: 1.3em;
}
@media only screen and (max-width: 1023px) {
  .rule {
    font-size: 3.2478632479vw;
  }
  .rule-item + .rule-item {
    border-width: 2px;
  }
  .rule-tit {
    font-size: 4.0170940171vw;
  }
}